import React, { Component } from "react";
import localforage from "localforage";
import { createGlobalStyle } from "styled-components";
import FileReaderInput from "react-file-reader-input";
import { ReactReader } from "./modules";
import {
  Container,
  ReaderContainer,
  Bar,
  LogoWrapper,
  Logo,
  GenericButton,
  CloseIcon,
  FontSizeButton,
  ButtonWrapper
} from "./Components";

const storage = global.localStorage || null;

const DEMO_URL = "";
const DEMO_NAME = "";

const GlobalStyle = createGlobalStyle`
  * {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
    margin: 0;
    padding: 0;
    color: inherit;
    font-size: inherit;
    font-weight: 300;
    line-height: 1.4;
    word-break: break-word;
  }
  html {
    font-size: 62.5%;
  }
  body {
    margin: 0;
    padding: 0;
    min-height: 100vh;
    font-size: 1.8rem;
    background: #333;
    position: absolute;
    height: 100%;
    width: 100%;
    color: #fff;
  }
  #tran-a {
    position: absolute;
    top: 0;
    left: 0;
    /* padding: 20px; */
    /* background: #565656; */
    z-index: 4;

    width: 100%;
    height: 100%;
  }
  #translateLayer-border {
  position: absolute;
  border: 27px solid #777;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  }
  #translateLayer {
  font-size: 16px;
    position: absolute;
    top: 0;
    left: 1rem;
    right: 1rem;
    bottom: 1rem;
    -webkit-transition: all 0.6s ease;
    transition: all 0.6s ease;
    0 0 5px rgba(0,0,0,.3): ;
    opacity: 0;
    margin: 27px 17px;
  }
  a#tran-a {text-decoration: none;}
  #translateLayer {opacity: 0; }
  #translateLayer.show {opacity: 1;}
  .btns {
  display: flex;
  justify-content: space-between;
  width : 100%;
  }
  #translateLayer button {
  z-index: 1;
  display:none;
  }
  #translateLayer.show button {
  z-index: 3
  }
  .firstLayer button {
  z-index: 2
  }
`;
let apiprocread_cache = [];
let apiprocread_cache2 = [];

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fullscreen: false,
      localFile: apiprocread_cache || null,
      localName: null,
      location:
        storage && storage.getItem("epub-location")
          ? storage.getItem("epub-location")
          : 2,

      location2:
        storage && storage.getItem("epub-location")
          ? storage.getItem("epub-location")
          : 2,
      localFile2: apiprocread_cache2 || null,
      localName2: null,
      largeText: false
    };
    this.rendition = null;
  }

  componentDidMount() {
    localforage.getItem('apiprocread_cache').then((value) => {
      // This code runs once the value has been loaded
      // from the offline store.
      console.log(value);
      this.setState({localFile: value});
    }).catch(function(err) {
      // This code runs if there were any errors
      console.log(err);
    });
    localforage.getItem('apiprocread_cache2').then((value) => {
      // This code runs once the value has been loaded
      // from the offline store.
      console.log(value);
      this.setState({localFile2: value});
    }).catch(function(err) {
      // This code runs if there were any errors
      console.log(err);
    });
  }

  toggleFullscreen = () => {
    this.setState(
      {
        fullscreen: !this.state.fullscreen
      },
      () => {
        setTimeout(() => {
          const evt = document.createEvent("UIEvents");
          evt.initUIEvent("resize", true, false, global, 0);
        }, 1000);
      }
    );
  };

  onLocationChanged = location => {
    console.log('location')
    this.setState(
      {
        location
      },
      () => {
        storage && storage.setItem("epub-location", location);
      }
    );
  };
  onLocationChanged2 = location => {
    this.setState(
      {
        location2: location
      },
      () => {
        storage && storage.setItem("epub-location2", location);
      }
    );
  };
  onToggleFontSize = () => {
    const nextState = !this.state.largeText;
    this.setState(
      {
        largeText: nextState
      },
      () => {
        this.rendition.themes.fontSize(nextState ? "140%" : "100%");
      }
    );
  };

  getRendition = rendition => {
    // Set inital font-size, and add a pointer to rendition for later updates
    const { largeText } = this.state;
    this.rendition = rendition;
    rendition.themes.fontSize(largeText ? "140%" : "100%");
  };

  handleChangeFile = async (event, results) => {
    if (results.length > 0) {
      const [e, file] = results[0];
      if (file.type !== "application/epub+zip") {
        return alert("Unsupported type");
      }
      const localFile = e.target.result
      await localforage.setItem('apiprocread_cache', localFile).then(function (value) {
        // Do other things once the value has been saved.
        // console.log(value);
      }).catch(function(err) {
        // This code runs if there were any errors
        console.log(err);
      });
      this.setState({
        localFile: localFile,
        localName: file.name,
        location: null
      });
    }
  };
  handleChangeFile2 = async (event, results) => {
    if (results.length > 0) {
      const [e, file] = results[0];
      if (file.type !== "application/epub+zip") {
        return alert("Unsupported type");
      }
      const localFile = e.target.result
      await localforage.setItem('apiprocread_cache2', localFile).then(function (value) {
        // Do other things once the value has been saved.
        // console.log(value);
      }).catch(function(err) {
        // This code runs if there were any errors
        console.log(err);
      });
      this.setState({
        localFile2: localFile,
        localName2: file.name,
        location2: null
      });
    }
  };
  mouse = (e)  => {
    console.log('DOWN ',e,e.target.tagName);
    if(e.target.tagName === 'BUTTON' ||
      e.target.getAttribute('id') !== 'translateLayer-border'){
      // e.stopPropagation();
    } else {
      e.target.classList.toggle( 'show')
      document.getElementById('translateLayer').classList.toggle( 'show')
    }
  }
  click = (e)  => {
    console.log(e,e.target.tagName);
    if(e.target.tagName === 'BUTTON' ||
      e.target.getAttribute('id') !== 'translateLayer-border'){
      // e.stopPropagation();
    } else {
      e.target.classList.toggle( 'show')
      document.getElementById('translateLayer').classList.toggle( 'show')
    }
  }
  render() {
    const {
      fullscreen,
      location,
      location2,
      localFile,
      localFile2,
      localName,
      localName2
    } = this.state;

    return (
      <Container>
        <GlobalStyle />
        <Bar>
          <ButtonWrapper className="btns">
            <FileReaderInput as="buffer" onChange={this.handleChangeFile}>
              <GenericButton>Upload epub</GenericButton>
            </FileReaderInput>
            <FileReaderInput as="buffer" onChange={this.handleChangeFile2}>
              <GenericButton>Upload Translated epub</GenericButton>
            </FileReaderInput>
          </ButtonWrapper>
        </Bar>
        <ReaderContainer fullscreen={fullscreen}>
          <ReactReader
            className="firstLayer"
            url={localFile || DEMO_URL}
            title={localName || DEMO_NAME}
            location={location}
            locationChanged={this.onLocationChanged}
            getRendition={this.getRendition}
          />
          <a id="tran-a" href="javascript:" onClick={this.click} onMouseDown={this.mouse}>
            <div id="translateLayer-border"></div>
            <div id="translateLayer">
              {localFile2 ? (
                <ReactReader
                  url={localFile2 || DEMO_URL}
                  title={localName2 || DEMO_NAME}
                  location={location2}
                  locationChanged={this.onLocationChanged2}
                  getRendition={this.getRendition}
                />
              ) : null}
            </div>
          </a>
        </ReaderContainer>
      </Container>
    );
  }
}

export default App;
